(function() {
    'use strict';

    angular
        .module('flavido')
        .controller('StudentCourseDiscussionController', StudentCourseDiscussionController);

    /** @ngInject */
    function StudentCourseDiscussionController(CommonInfo, $log, $http, $stateParams, growl, $state) {
        var vm = this;
        var selectedCourseId;
        var studentInfo;

        vm.selectedCourseName;

        vm.startDiscussion = startDiscussion;
        vm.showThread = showThread;

        activate();

        function activate() {
            selectedCourseId = $stateParams.id;
            vm.selectedCourseName = $stateParams.name;
            studentInfo = CommonInfo.getInfo('studentInfo');
            if(!studentInfo || !studentInfo.userId){
                $state.go('main');
            }
            getAllDiscussions();
        }

        function getAllDiscussions() {
            $http.post(CommonInfo.getAppUrl() + "/getalldiscussions", { courseId: selectedCourseId }).then(
                function(response) {
                    if (response && response.data) {
                        if (response.data.status == 1) {
                            vm.courseDiscussions = response.data.data;
                        } else if (response.data.status == 2) {
                            growl.info(response.data.message);
                        }
                    } else {
                        growl.info('There is some issue, please try after some time');
                    }
                },
                function(response) {
                    growl.info('There is some issue, please try after some time');
                }
            );
        }

        function startDiscussion() {
            if (vm.newDiscussion.postTitle && vm.newDiscussion.postDesc) {
                vm.addNewDiscussion = false;
                vm.newDiscussion.studentId = studentInfo.userId;
                vm.newDiscussion.courseId = selectedCourseId;
                $http.post(CommonInfo.getAppUrl() + "/creatediscussion", vm.newDiscussion).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1) {
                                getAllDiscussions();
                            } else if (response.data.status == 2) {
                                $log.log(response.data.message);
                            }
                        } else {
                            $log.log('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        $log.log('There is some issue, please try after some time');
                    }
                );
            }
        }

        function showThread(discussion){
            if(discussion) {
                $state.go('threads', { id: discussion.id, name: discussion.postTitle });
            }
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('flavido')
        .controller('StudentCourseUnitController', StudentCourseUnitController);

    /** @ngInject */
    function StudentCourseUnitController(CommonInfo, $http, $log, $state, $stateParams, $mdSidenav, $scope, $timeout, $sce, growl, _, moment) {
        var vm = this;
        var selectedCourseId;
        var selectedUnitId;
        var selectedCourseName;
        var studentInfo;
        var submitAttempt;
        //var selectedUnitName;

        vm.showReviewPage = false;
        vm.userCurrentQuestion = [];

        vm.toggleLeft = buildDelayedToggler('left');
        vm.closeSide = closeSide;

        vm.getCourseUnit = getCourseUnit;
        vm.getReviewPage = getReviewPage;
        vm.submitReview = submitReview;
        vm.showRelatedDiscussion = showRelatedDiscussion
        vm.submitExam = submitExam;

        activate();

        function activate() {
            studentInfo = CommonInfo.getInfo('studentInfo');
            if(!studentInfo || !studentInfo.userId){
                $state.go('main');
            }
            var startCourse = CommonInfo.getInfo('startCourse');
            selectedCourseId = startCourse.courseId;
            selectedCourseName = $stateParams.courseName;
            selectedUnitId = startCourse.unitId;
            //selectedUnitName = $stateParams.unitName;
            getUnitDetails(selectedUnitId);
            getCourseDetails();
        }

        function getUnitDetails(unitId) {
            if (studentInfo && studentInfo.name && studentInfo.email) {
                CommonInfo.setInfo('startCourse', { unitId: unitId, courseId: selectedCourseId });
                $http.post(CommonInfo.getAppUrl() + "/getunitdetailsbyunit_id", { id: unitId, userName: studentInfo.name, userEmail: studentInfo.email }).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1) {
                                vm.unit = response.data.data;
                                vm.unit.unitDescription = angular.isString(vm.unit.unitDescription) ? $sce.trustAsHtml(vm.unit.unitDescription) : vm.unit.unitDescription;
                                vm.unit.videoHtml = angular.isString(vm.unit.videoHtml) ? $sce.trustAsHtml(vm.unit.videoHtml) : vm.unit.videoHtml;
                                if (angular.isString(vm.unit.downloadLink))
                                    vm.unit.downloadLink = JSON.parse(vm.unit.downloadLink);
                                if (vm.unit.testId) {
                                    getTest(vm.unit.testId);
                                }
                            } else if (response.data.status == 2) {
                                $log.log(response.data.message);
                            }
                        } else {
                            $log.log('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        $log.log('There is some issue, please try after some time');
                    }
                );
                // vm.unit = _.filter(vm.course.units, { 'id', unitId });
                // vm.unit.unitDescription = angular.isString(vm.unit.unitDescription) ? $sce.trustAsHtml(vm.unit.unitDescription) : vm.unit.unitDescription;
                // vm.unit.videoHtml = angular.isString(vm.unit.videoHtml) ? $sce.trustAsHtml(vm.unit.videoHtml) : vm.unit.videoHtml;
                // if (angular.isString(vm.unit.downloadLink))
                //     vm.unit.downloadLink = JSON.parse(vm.unit.downloadLink);
                // if (vm.unit.testId) {
                //     getTest(vm.unit.testId);
                // }
            }
        }

        function getTest(testId) {
            $http.post(CommonInfo.getTestSeriesAppUrl() + "/test/byId", { testId: testId }).then(
                function(response) {
                    if (response && response.data) {
                        if (!response.data.Error) {
                            vm.unit.test = response.data.test;
                            getTestQuestions(testId);
                        } else if (response.data.status == 2) {
                            $log.log(response.data.message);
                        }
                    } else {
                        $log.log('There is some issue, please try after some time');
                    }
                },
                function(response) {
                    $log.log('There is some issue, please try after some time');
                }
            );
        }

        function getTestQuestions(testId) {
            $http.post(CommonInfo.getTestSeriesAppUrl() + "/exam/byId", { testId: testId }).then(
                function(response) {
                    if (response && response.data) {
                        if (!response.data.Error) {
                            vm.unit.test.questions = response.data.questions;
                            getUserTestInfo();
                        } else if (response.data.status == 2) {
                            $log.log(response.data.message);
                        }
                    } else {
                        $log.log('There is some issue, please try after some time');
                    }
                },
                function(response) {
                    $log.log('There is some issue, please try after some time');
                }
            );
        }

        function getUserTestInfo() {
            var data = {
                userId: studentInfo.userId,
                testId: vm.unit.test.id
            };
            $http.post(CommonInfo.getTestSeriesAppUrl() + '/exam/userInfo', data).then(function(response) {
                if (response && response.data && !response.data.Error) {
                    vm.unit.test.userInfo = response.data.userTestInfo;
                    if (vm.unit.test && vm.unit.test.userInfo && vm.unit.test.userInfo.status == 'pending' && vm.unit.test.userInfo.timeRemaining == 0) {
                        alert('Your exam time is over, press ok to submit exam')
                        submitExam(true);
                    } else {
                        //vm.timer = vm.exam.userInfo.timeRemaining || vm.test.duration;
                        //vm.showLangChoice = vm.exam.questions[0].questionText ? 1 : 0;
                        if (vm.showLangChoice)
                            vm.selectedLang = vm.unit.test.userInfo.selectedLang ? vm.unit.test.userInfo.selectedLang : 0;
                        else
                            vm.selectedLang = 1;
                        _.forEach(vm.unit.test.questions, function(value, key) {
                            vm.userCurrentQuestion[key] = _.find(vm.unit.test.userInfo.answers, { 'questionId': value.id }) || {
                                questionId: value.id,
                                answer: '',
                                isMarked: false
                            };
                        });
                    }
                } else {
                    growl.info('Some error occured, try after some time');
                }
            }, function(response) {
                growl.info('Some error occured, try after some time');
            });
        }

        function submitExam(isForced, status) {
            status = status || 'completed';
            var data = {
                userId: studentInfo.userId,
                testId: vm.unit.test.id,
                answers: vm.userCurrentQuestion,
                status: status,
                timestamp: moment(),
                selectedLang: vm.selectedLang
            };
            if (status == 'completed') {
                vm.isExamEnded = true;
            }
            if (isForced || status == 'pending' || confirm('Are you sure, you want to submit(final) your answers')) {
                $http.post(CommonInfo.getTestSeriesAppUrl() + '/exam/submit', data).then(function(response) {
                    if (response && response.data) {
                        if (status == 'completed') {
                            growl.success('Your answers saved successfully');
                        } else {
                            if (status != 'pending') {
                                if (submitAttempt <= 3) {
                                    submitAttempt++;
                                    submitExam(true);
                                } else {
                                    growl.info('Unable to submit due to some server error, please try after some time');
                                }
                            }
                        }
                    }
                }, function(response) {
                    if (status != 'pending') {
                        if (submitAttempt <= 3) {
                            submitAttempt++;
                            submitExam(true);
                        } else {
                            growl.info('Unable to submit due to some server error, please try after some time');
                        }
                    }
                });
            }
        }

        function getCourseDetails() {
            $http.post(CommonInfo.getAppUrl() + "/getcoursedetailsbycourse_id", { courseId: selectedCourseId, studentId: studentInfo.userId }).then(
                function(response) {
                    if (response && response.data) {
                        if (response.data.status == 1) {
                            if (selectedCourseName == response.data.data[0].title.replace(/ /g, "-")) {
                                vm.course = response.data.data[0];
                                if(vm.course && vm.course.courseCurriculum && vm.course.units.length > 0) {
                                    var units = [];
                                    var unitIds = vm.course.courseCurriculum.split(',');
                                    _.forEach(unitIds, function(value, key){
                                        console.log(value)
                                        units.push(_.find(vm.course.units, { 'id': parseInt(value) }));
                                    });
                                    console.log(units);
                                    vm.course.units = units;
                                }
                                vm.review = {
                                    reviewRating: vm.course.reviewRating,
                                    reviewTitle: vm.course.reviewTitle,
                                    reviewDetails: vm.course.reviewDetails
                                };
                            }
                        } else if (response.data.status == 2) {
                            growl.info(response.data.message);
                            $state.go('dashboard');
                        }
                    } else {
                        $log.log('There is some issue, please try after some time');
                    }
                },
                function(response) {
                    $log.log('There is some issue, please try after some time');
                }
            );
        }

        function getCourseUnit(unit) {
            closeSide();
            vm.showReviewPage = false;
            var id = selectedCourseId + ':' + unit.id;
            $state.transitionTo('startCourse', { id: id, courseName: selectedCourseName, unitName: unit.name.replace(/ /g, "-") }, { notify: false });
            getUnitDetails(unit.id);
        }

        function getReviewPage() {
            closeSide();
            vm.showReviewPage = true;
            vm.unit.name = "Course Review";
        }

        function submitReview() {
            if (vm.review.reviewRating && vm.review.reviewTitle) {
                vm.review.courseId = selectedCourseId;
                vm.review.studentId = studentInfo.userId;
                $http.post(CommonInfo.getAppUrl() + "/createupdatereview", vm.review).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1) {
                                growl.success('Review submited successfuly');
                            } else if (response.data.status == 2) {
                                growl.log(response.data.message);
                            }
                        } else {
                            growl.log('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        growl.log('There is some issue, please try after some time');
                    }
                );
            }
        }

        function showRelatedDiscussion() {
            $state.go('discussions', { id: selectedCourseId, name: selectedCourseName });
        }

        function debounce(func, wait) {
            var timer;

            return function debounced() {
                var context = $scope,
                    args = Array.prototype.slice.call(arguments);
                $timeout.cancel(timer);
                timer = $timeout(function() {
                    timer = undefined;
                    func.apply(context, args);
                }, wait || 10);
            };
        }

        function buildDelayedToggler(navID) {
            return debounce(function() {
                $mdSidenav(navID)
                    .toggle();
            }, 200);
        }

        function closeSide() {
            $mdSidenav('left').close();
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('flavido')
        .controller('TestSeriesController', TestSeriesController);

    /** @ngInject */
    function TestSeriesController(CommonInfo, $state, $http, $mdDialog, $scope, $log, growl) {
        var vm = this;

        vm.showTests = showTests;

        vm.loginStage = 1;
        vm.student = {
            student_id: '',
            emailorphone: '',
            password: '',
            phone: ''
        };
        vm.verification = {
            OTP: '',
            student_id: '',
            phone: ''
        };

        vm.login = login;
        vm.otpVerification = otpVerification;
        vm.sendOTP = sendOTP;

        activate();

        function activate() {
            getAllTestSeries();
        }

        function getAllTestSeries() {
            $http.get(CommonInfo.getTestSeriesAppUrl() + "/testSeries/all").then(
                function(response) {
                    if (response && response.data) {
                        if (!response.data.Error) {
                            vm.testSeriesList = response.data.testSeries;
                        } else {
                            $log.log(response.data.message);
                        }
                    } else {
                        $log.log('There is some issue, please try after some time');
                    }
                },
                function(response) {
                    $log.log('There is some issue, please try after some time');
                }
            );
        }

        function showTests(evt, series) {
            var studentInfo = CommonInfo.getInfo('studentInfo');
            if(studentInfo && studentInfo.userId) {
                series = series ? series : CommonInfo.getInfo('selectedSeries');
                $state.go('testList', { seriesName: series.name.replace(/ /g, '-'), seriesId: series.id });
            } else {
                CommonInfo.setInfo('selectedSeries', series);
                 $mdDialog.show({
                    targetEvent: evt,
                    scope: $scope.$new(),
                    templateUrl: 'app/main/login.tmpl.html',
                    parent: angular.element(document.body),
                    clickOutsideToClose:true
                })
                .then(function(answer) {
                    $scope.status = 'You said the information was "' + answer + '".';
                }, function() {
                    $scope.status = 'You cancelled the dialog.';
                });
            }
        }

         function login() {
            if (vm.student.emailorphone && vm.student.password) {
                $http.post(CommonInfo.getAppUrl() + "/studentlogin", vm.student).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1) {
                                $mdDialog.hide();
                                //growl.success('Login Successfuly');
                                CommonInfo.setInfo('studentInfo', response.data.data);
                                showTests()
                            } else if (response.data.status == 3) {
                                CommonInfo.setInfo('studentInfo', response.data.data);
                                vm.verification.student_id = response.data.data.userId;
                                vm.student.student_id = response.data.data.userId;
                                if (response.data.message == '2') {
                                    vm.loginStage = 2;
                                }
                            } else if (response.data.status == 2) {
                                growl.info(response.data.message);
                            }
                        } else {
                            growl.warning('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        growl.warning('There is some issue, please try after some time');
                    }
                );
            }
        }

        function otpVerification() {
            if (vm.verification && vm.verification.OTP && vm.verification.student_id && vm.verification.phone) {
                $http.post(CommonInfo.getAppUrl() + "/verifyotp", vm.verification).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1) {
                                CommonInfo.setInfo('studentInfo', response.data.data);
                                showTests();
                            } else if (response.data.status == 2) {
                                growl.info(response.data.message);
                            }
                        } else {
                            growl.warning('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        growl.warning('There is some issue, please try after some time');
                    }
                );
            }
        }

        function sendOTP() {
            if (vm.verification && vm.verification.phone && vm.verification.student_id) {
                $http.post(CommonInfo.getAppUrl() + "/verifymobileandsendotp", vm.verification).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1) {
                                growl.success('OTP send');
                                vm.showOtpField = true;
                            } else if (response.data.status == 2) {
                                growl.info(response.data.message);
                            }
                        } else {
                            growl.warning('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        growl.warning('There is some issue, please try after some time');
                    }
                );
            }
        }
    }
})();

